import PropTypes from "prop-types";

export default function Section({
    id = "",
    clases = [],
    titulo = "",
    Contenido,
}) {
    return (
        <section id={id} className={clases.join(" ")}>
            <h1>{titulo}</h1>
            <Contenido />
        </section>
    );
}

Section.propTypes = {
    id: PropTypes.string,
    clases: PropTypes.arrayOf(PropTypes.string),
    titulo: PropTypes.string,
};
